.card {
	position: relative;
	padding-bottom: 5px;
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		@include hover {
			& ~ .card__title {
				color: $red; } } }
	&__img {
		position: relative;
		padding-bottom: 62%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center; }
	&__badges {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		pointer-events: none;
		z-index: 2;
		padding: 20px 20px 20px 24px; }
	&__date {
		margin-top: 18px; }
	&__badge {
		margin: 4px 4px 0 0;
		pointer-events: initial; }
	&__title {
		margin-top: 14px;
		max-height: 84px;
		overflow: hidden;
		transition: color .3s;
		@include f(20px,600,1.4);
		display: block; } }

.card-lg {
	position: relative;
	min-width: 100%;
	color: $white;
	padding-bottom: 63.49%;
	&_ml {
		margin-left: -40px; }
	&_middle {
		padding-bottom: 44.7%; }
	&_black {
		color: $black; }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		&_top {
			top: -16px; }
		&_right {
			right: -16px; }
		&_left {
			left: -16px; }
		&_bottom {
			right: -16px; } }
	&__wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding: 108px 40px 40px;
		.card-lg_middle & {
			padding: 27px 40px 40px; } }
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		@include hover {
			& ~ .card-lg__wrap .card-lg__title {
				color: $red; } } }
	&__head {
		position: relative;
		z-index: 2;
		pointer-events: none;
		flex-wrap: wrap;
		align-items: center;
		display: flex;
		&:not(:last-child) {
			margin-bottom: 40px; }
		& > * {
			pointer-events: initial; }
		.card-lg_middle & {
			margin-bottom: 64px; } }
	&__badge {
		margin: 4px 30px 0 0; }
	&__title {
		max-width: 400px;
		max-height: 220px;
		overflow: hidden;
		@include f(32px,700,1.375);
		color: currentColor;
		transition: color .3s; } }

.card-sm {
	position: relative;
	display: flex;
	&:not(:last-child) {
		margin-bottom: 24px;
		padding-bottom: 24px;
		border-bottom: 1px solid #F2F2F2; }
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 25px;
		width: 100%;
		@include hover {
			& ~ .card-sm__wrap .card-sm__title {
				color: $red; } } }
	&__img {
		flex-shrink: 0;
		width: 96px;
		height: 88px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		& + .card-sm__wrap {
			padding-left: 24px; } }
	&__wrap {
		flex-grow: 1; }
	&__title {
		max-height: 66px;
		overflow: hidden;
		transition: color .3s;
		@include f(16px,600,1.375); }
	&__footer {
		position: relative;
		align-items: center;
		flex-wrap: wrap;
		display: flex; }
	&__badge {
		margin: 5px 16px 0 0; }
	&__date {
		margin-top: 5px; } }

.card-simple {
	&__top {
		align-items: center;
		flex-wrap: wrap;
		display: flex;
		margin-bottom: 40px; }
	&__badge {
		margin: 5px 16px 0 0; }
	&__date {
		margin-top: 5px; }
	&__title {
		max-height: 160px;
		overflow: hidden;
		@include f(28px,700,1.43);
		color: currentColor;
		transition: color .3s;
		display: block;
		@include hover {
			color: $red; } } }
